  ///////////////////////////////////////////////////////////////////////////////////////////////////////////    navigation bar
  



  .navigation {
    &__checkbox {
      display: none;
    }
  
    &__button {
      background-color: #0e9fd8;
      height: 7rem;
      width: 7rem;
      position: fixed;
      top: 6rem;
      right: 6rem;
      border-radius: 50%;
      z-index: 2000;
      box-shadow: 0 1rem 3rem rgba(black, 0.1);
      text-align: center;
      cursor: pointer;
    }
  
    &__background {
      height: 6rem;
      width: 6rem;
      border-radius: 50%;
      position: fixed;
      top: 6.5rem;
      right: 6.5rem;
      background-image: radial-gradient(
        #0e9fd873,
        #0e9fd873,
      );
      z-index: 1000;
      transition: transform 0.8s cubic-bezier(0,0,0,0);
    }
  
    &__nav {
      height: 100vh;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 1500;
  
      opacity: 0;
      width: 0;
      transition: all 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    }
  
    &__list {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      list-style: none;
      text-align: center;
    }
  
    &__item {
      margin: 1rem;
    }
  
    &__link {
      &:link,
      &:visited {
        display: inline-block;
        font-size: 3rem;
        font-weight: 300;
        padding: 1rem 2rem;
        color: white;
        text-decoration: none;
        text-transform: uppercase;
        background-image: linear-gradient(
          120deg,
          transparent 0%,
          transparent 50%,
          white 50%
        );
        background-size: 240%;
        transition: all 0.4s;
  
        // span {
        //   margin-right: 1.5rem;
        //   display: inline-block;
        // }
      }
  
      &:hover,
      &:active {
        background-position: 100%;
        color: #0e9fd8;
        transform: translateX(1rem);
      }
    }
  
    // FUNCTIONALITY
  
    &__checkbox:checked ~ &__background {
      transform: scale(80);
    }
  
    &__checkbox:checked ~ &__nav {
      opacity: 1;
      width: 100%;         ///////////////////////////////////////////////////////////////////////////////it was on 100%, I'm working on it to place the list under the button
    }
  
    // ICON
  
    &__icon {
      position: relative;
      margin-top: 3.5rem;
  
      &,
      &::before,
      &::after {
        width: 3rem;
        height: 2px;
        background-color: #c8c8c8c8;
        display: inline-block;
      }
  
      &::before,
      &::after {
        content: "";
        position: absolute;
        left: 0;
        transition: all 0.2s;
      }
  
      &::before {
        top: -0.8rem;
      }
  
      &::after {
        top: 0.8rem;
      }
    }
  
    &__button:hover &__icon::before {
      top: -1rem;
    }
  
    &__button:hover &__icon::after {
      top: 1rem;
    }
  
    &__checkbox:checked + &__button &__icon {
      background-color: transparent; // we are putting transparent because if we remove it, it will affect the before and after
      transition: all 0.4s;
    }
  
    &__checkbox:checked + &__button &__icon::before {
      top: 0;
      transform: rotate(215deg);
      transition: all 0.8s;
    }
  
    &__checkbox:checked + &__button &__icon::after {
      top: 0;
      transform: rotate(-215deg);
      transition: all 0.8s;
    }
  }
  
  // transform-origin helps in the dermine from which point the rotation will start from
  
  








  
  










  
