.card {
  // FOR THE FUNCTIONNALITY

  perspective: 150rem; // the low the perspective the more the drama in the transition   [perspective helps in the good looking rotation of anything]
  -moz-perspective: 150rem;
  position: relative;
  height: 28rem; // we put it two times because it got lost and was rotating in another quadrant of col-1-of-3 due to the fixation of the positions
  width: 28rem;
  margin: 20px 5px 20px 5px ;

  &__side {
    font-size: 2rem;
    height: 28rem;
    width: 28rem;
    transition: all 1s ease;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    backface-visibility: hidden; //this time is to hide one card and show another, not fixing animation checking
    border-radius: 0.3rem;
    overflow: hidden; // this is to make the border radius work because the image go over


    &--front {
      background-color: #F3F4F6;
      z-index: 1;
    }

    &--back {
      background-color: green;
      z-index: 0;

      transform: rotateY(-180deg);

      &-1 {
        background-color: #F3F4F6;
      }
    }
  }
  &:hover &__side--front {
    transform: rotateY(180deg);
  }

  &:hover &__side--back {
    transform: rotateY(0);
  }


    // FRONT SIDE STYLING

    &__picture {
      background-size: cover;
      height: 23rem;
  
      background-blend-mode: screen; // thi is a new functionality it does not work for internet expore and other low browser
  
      &--1 {
        background-image: url(../assets/digital1.jpeg);
      }
    }

    &__heading {
      font-size: 2.8rem;
      font-weight: 300;
      text-transform: uppercase;
      text-align: right;
      color: white;
      position: absolute;
      top: 12rem;
      right: 2rem;
      width: 75%;
    }
  
    &__heading-span {
      padding: 1rem 1.5rem;
      -webkit-box-decoration-break: clone;
      box-decoration-break: clone; // for th alignment of the text in the box (the sea being up, explorer down)
  
      &--1 {
        background-image: linear-gradient(
          to right bottom,
          rgba(#ffb900, 0.85),
          rgba(#ff7730, 0.85)
        );
      }
    }



    &__details {
      padding: 1.5rem;

      &__title {
        text-align: center;
        font-weight: bold;
        font-size: 10px;
      }
    }
  
    // For the back
  
    &__cta {
      // the cta stands for call for action
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 90%;
      text-align: center;
    }
  
    &__price-box {
      text-align: center;
      color: black;
    }
  
    &__price-only {
      font-size: 1.4rem;
      text-transform: uppercase;
    }
  
    &__price-value {
      font-size: 6rem;
      font-weight: 100;
    }
  
  
  
  
  
    // @include respond(tab-portrait) {
      @media only screen and (max-width: 56.25em), only screen and (hover: none)   // this hover this is to specify that it has to follow the following criteria if the hoverability is inexistant
      {
      height: auto;
      border-radius: 3rem;
      background-color: white;
      box-shadow: 0 1.5rem 4rem rgba(black, 0.15rem);
  
      &__side {
        height: auto;
        position: relative;
        border-radius: 0.3rem;
        box-shadow: none;
  
        // &--back {
        //   transform: rotateY(0);
        //   clip-path: polygon(0 15%, 100% 0, 100% 100%, 0 100%);
        // }
      }
  
      &:hover &__side--front {
        transform: rotateY(0);
      }
  
      &__details {
          padding: 1rem 3rem;
      }
  
      // FRONT SIDE STYLING
  
      &__cta {
        // the cta stands for call for action
        position: relative;
        top: 0%;
        left: 0%;
        transform: translate(0%);
        width: 100%;
        padding: 7rem 4rem 4rem 4rem;
      }
  
      &__price-box {
        margin-bottom: 3rem;
      }
  
      &__price-value {
        font-size: 4rem;
      }
    }
  
}









